import apiFetch from "@wordpress/api-fetch"
import {Button,PanelBody,PanelRow,TextControl} from "@wordpress/components"
import { InnerBlocks, InspectorControls,MediaUpload,MediaUploadCheck } from "@wordpress/block-editor"
import { registerBlockType } from "@wordpress/blocks"
import {useEffect} from "@wordpress/element" 

registerBlockType("fitnessblocktheme/fitnesschoosesection",{
    title:"Fitness Choose Us Section",
    supports:{
        align:["full"]
    },
    attributes:{
        align:{type:"string",default:"full"},
        imgID:{type:"number"},
        imgURL:{type:"string",default:fitnesschoosesection.fallbackImage},
        sectionHeading:{type:"string",default:fitnesschoosesection.headingtext},
        sectionTitle:{type:"string",default:fitnesschoosesection.titletext}
    },
    edit:EditComponent,
    save:SaveComponent
})
function EditComponent(props){
    useEffect(
        function(){
            if(props.attributes.imgID){
                async function go(){
                    const response=await apiFetch({
                        path:`/wp/v2/media/${props.attributes.imgID}`,
                        method:"GET"
                    })
                    props.setAttributes({imgURL:response.media_details.sizes.full.source_url})
                }
                go()
            }
        
    },[props.attributes.imgID])

    function onFileSelect(x){
        props.setAttributes({imgID:x.id})
    }
    function onHeadingChange(x){
        props.setAttributes({sectionHeading:x})

    }
    function onTitleChange(x){
        props.setAttributes({sectionTitle:x})
    }

    return (
        <>
            <InspectorControls>
                <PanelBody title="Section Heading">
                    <TextControl 
                        onChange={onHeadingChange}
                        value={props.attributes.sectionHeading}
                        type="text"
                    />
                </PanelBody>
                <PanelBody title="Section Title">
                    <TextControl 
                        onChange={onTitleChange}
                        value={props.attributes.sectionTitle}
                        type="text"
                    />
                </PanelBody>
                <PanelBody title="Background" initialOpen={true}>
                    <MediaUploadCheck>
                        <MediaUpload 
                            onSelect={onFileSelect} 
                            value={props.attributes.imgID} 
                            render={({open})=>{
                                return <Button onClick={open}>Choose Image</Button>
                            }}
                        />
                    </MediaUploadCheck>
                </PanelBody>
            </InspectorControls>
            <div className="choose-us" style={{backgroundImage:`url('${props.attributes.imgURL}')`}}>
                <div className="container">
                    <div className="choose-us-inr">
                    <div className="row d-flex align-items-center">
                        <div className="col-xxl-6 col-md-12 col-12">
                            <div className="text-area text-center text-xxl-start mb-4 mb-xxl-0">
                                <p className="font-secondary">{`${props.attributes.sectionTitle}`}</p>
                                <h2>{`${props.attributes.sectionHeading}`}</h2>
                            </div>
                        </div>
                        <div className="col-xxl-6 col-md-12 col-12">
                            <div className="row">
                                <InnerBlocks allowedBlocks={['fitnessblocktheme/fitnesschoosecard']}/>
                            </div>
                        </div>
                    </div>
                    </div>
                </div>
            </div>
        </>
    );
}
function SaveComponent(){
    return <InnerBlocks.Content/>
}